* {
    color: white;

    li {
        list-style: none;
    }

    a {
        text-decoration: none;
        color: white;
        cursor: pointer;
    }

    body {
        background-color: rgb(22, 22, 22);
        font-family: Jost, Arial, Helvetica, sans-serif;

        .main {
            display: flex;
            width: 100%;
            height: 100vh;

            .left-bar {
                position: relative;
                display: flex;
                flex-direction: column;
                width: 25%;
                margin: 50px 25px;

                h1 {
                    font-size: 50px;
                    text-align: center;
                }

                a {
                    padding: 15px;
                    font-size: 16px;
                    text-transform: uppercase;
                    text-align: center;
                }

                a:hover {
                    background: #ffcc0d;
                }
            }
            .page{
                position: relative;
                margin: 10px;
            }
            .page::before {
                content: '';
                position: absolute;
                top: 60px;
                left: 0;
                right: 0;
                bottom: 0;
                margin-right:10px ;
                background-color: #000;
                width: 6px;
            }

            .page {
                width: 100%;
                display: flex;
                flex-direction: column;
                align-items: center;
                div{
                    width: 90%;
                    margin-top: 5%;
                }
            }
        }
    }
}